<template>
  <el-drawer
    title=""
    :modal-append-to-body="false"
    size="50%"
    append-to-body
    :visible.sync="showDrawer"
    :direction="direction"
    class="y-dialog"
  >
    <el-col :span="24">
      <el-transfer
        :props="fields"
        v-model="value"
        ref="transfer"
        :titles="titles"
        :data="mergeColumns"
        @change="handleChange"
      ></el-transfer>
    </el-col>
  </el-drawer>
</template>

<script>
import { deepClone } from '@/utils/data'
export default {
  name: 'yFilterColumns',
  props: {
    drawer: {
      type: Boolean,
      default: false
    },
    direction: {
      type: String,
      default: 'rtl'
    },
    columns: {
      type: Array,
      default () {
        return []
      }
    },
    fields: {
      type: Object,
      default () {
        return {
          key: 'prop',
          label: 'i18n',
          disabled: 'disabled'
        }
      }
    },
    titles: {
      type: Array,
      default () {
        return ['', '']
      }
    }
  },
  computed: {
    mergeColumns: {
      get () {
        let arr = this.handleColumns(this.columns)
        return arr
      }
    },
    showDrawer: {
      get () {
        return this.drawer
      },
      set (val) {
        this.$emit('update:drawer', val)
      }
    }
  },
  watch: {
    value (val) {
      this.$emit('update:value', val)
    }
  },
  data () {
    return {
      value: [],
      copyColumns: []
    }
  },
  created () {
    this.copyColumns = deepClone(this.columns)
  },
  methods: {
    initValue (val) {
      this.value = val
    },
    handleColumns (columns) {
      let arr = []
      let cols = Array.from(columns).reduce((prev, curr) => {
        if (curr.isShow !== false || curr.isFilter) {
          prev.push(curr)
        }
        if (!curr.isFilter) {
          arr.push(curr.prop)
        }
        return prev
      }, [])
      this.initValue(arr)
      return cols
    },
    handleChange (value, direction, arr) {
      this.$emit('change', { value, direction })
    }
  }
}
</script>
<style scoped>
::v-deep .el-transfer-panel__list,
.el-transfer-panel__body {
  height: 100% !important;
}
::v-deep .el-transfer-panel__body {
  height: 500px !important;
}
::v-deep .el-drawer__body {
  padding-left: 50px;
}
</style>
