  background: #fff;
  padding: 32px 16px;
}
.pagination-container.hidden {
  display: none;
}
.el-table {
  ::v-deep thead {
    background-color: #515a6e !important;
  }
  ::v-deep th {
    background-color: #f8f8f9 !important;
  }
}

::v-deep .el-table__fixed-right {
  right:0 !important;
}
